.work-block {
   display: flex;
   border-bottom: 1px solid #E0E0E0;
   padding-top: 2rem;
   @include mobile{
      display: block;
   }

   &__img {
     padding: 0 1.125rem 0.9rem 0;
     flex: 0 0 15.3rem;
     display: block;
     @include mobile{
      
      img{
         display: block;
         min-width: 100%;
         object-fit:contain;
         vertical-align: bottom;
      }
     }
   }

}




.work-block-description {


&__title {
   font-family: $font-heebo;
   font-weight: 700;
   font-size: 1.875rem;
   line-height: 2.75rem;
   color:$color-dark;
   padding: 0 0 1rem 0;
}

&__info {
   color:$color-light;
   font-weight: 400;
   font-size: 1.25rem;
   line-height: 1.8rem;

   span{
      color:$color-white;
      font-weight: 900;
      font-size: 1.1rem;
      background-color: $color-dark;
      padding: 0 0.6rem;
      border-radius: 1rem;
      margin: 0 1.625rem 0 0;
   }
}

&__text {
   padding: 1rem 0 0 0;
}
}
